import React, { Component } from "react";
import { Page, Input, Button, ProgressBar } from "react-onsenui";
import ErrorMsg from "../ErrorMsg/ErrorMsg";
import LocationsPage from "../LocationsPage/LocationsPage";
import SignUpPage from "../SignUpPage/SignUpPage";
import axios from "axios";
import "./LoginPage.css";

const LOGIN_URI = "https://cors-anywhere.herokuapp.com/http://159.203.3.180/api/users/login";

class LoginPage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      email: "",
      password: "",
      token: localStorage.getItem("scavengerHuntTkn"),
      errors: [],
      isLoading: false
    };

    this.handleLogin = this.handleLogin.bind(this);
    this.openSignUpPage = this.openSignUpPage.bind(this);
    this.openLocationsPage = this.openLocationsPage.bind(this);

    // Skip the login page if the token exists and stored
    if (this.state.token) {
      this.openLocationsPage();
    }
  }

  openLocationsPage() {
    this.props.navigator.pushPage({
      component: LocationsPage,
      props: {
        key: `LOCATIONS_PAGE_${Date.now()}`,
        token: this.state.token
      }
    });
  }

  handleLogin() {
    this.setState({ isLoading: true });
    
    const { errors, token, ...payload } = this.state;

    axios
      .post(LOGIN_URI, payload)
      .then(res => {
        this.setState({
          token: res.data.token,
          isLoading: false
        });
        localStorage.setItem("scavengerHuntTkn", this.state.token);
        this.openLocationsPage();
      })
      .catch(err =>
        this.setState({
          errors: err.response.data,
          isLoading: false
        })
      );
  }

  openSignUpPage() {
    this.setState({
      email: "",
      password: "",
      errors: []
    });

    this.props.navigator.pushPage({
      component: SignUpPage,
      props: { key: "SIGNUP_PAGE" }
    });
  }

  render() {
    return (
      <Page>
        {this.state.isLoading && <ProgressBar indeterminate />}
        <div className="LoginPage">
          <img
            src="sask-polytech-logo-compressed.png"
            width="200"
            alt="Saskatchewan Polytechnic"
          />
          <section className="LoginPage-form">
            <p className="LoginPage-title">Scavenger Hunt</p>
            <Input
              value={this.state.email}
              onChange={e => this.setState({ email: e.target.value })}
              type="email"
              disable-auto-styling
              placeholder="Email"
              className="LoginPage-input"
            />
            <Input
              value={this.state.password}
              onChange={e => this.setState({ password: e.target.value })}
              type="password"
              disable-auto-styling
              placeholder="Password"
              className="LoginPage-input"
            />
            {this.state.errors &&
              this.state.errors.map((err, index) => {
                return <ErrorMsg key={index} message={err} />;
              })}

            <Button
              modifier="large"
              className="LoginPage-login_btn"
              onClick={this.handleLogin}
            >
              SIGN IN
            </Button>
            <Button
              modifier="large--quiet"
              className="LoginPage-signup_btn"
              onClick={this.openSignUpPage}
            >
              or create new account
            </Button>
          </section>
          <img
            src="team-logo-white.png"
            className="LoginPage-logo_bottom"
            width="100"
            alt="OOWEE"
          />
        </div>
      </Page>
    );
  }
}

export default LoginPage;
